
.tab-buttons {
	list-style: none;
	margin: 0;
	margin-bottom: -5px;
	margin-top: -3px;
	padding: 0;
}
.tab-buttons li {
	display: inline-block;
}
.tab-buttons li a {
	display: block;
	padding: 15px;
	padding-top: 10px;
	padding-bottom: 11px;
	text-decoration: none;
}
.tab-buttons li a:hover {
	text-decoration: none;
	background-color: white;
}
.tab-buttons li.active a {
	border-left: 3px solid #003540;
}
.tab-buttons li.active a {
	background-color: white;
}
@media (max-width: 700px) {
.tab-buttons li {
		width: calc(50% - 2px)
}
.tab-buttons {
		margin-top: 10px;
}
}



