
.v-select .dropdown-toggle {
  position: relative;
  background-color: white;
}
.section-wrapper {
    margin-top: 47px;
}
.section-outer {
    margin: auto;
    background-color: white;
}
section {
    padding: 20px;
    margin: auto;
    
    margin-bottom: 20px;
    background-color: white;
    position: relative;
}
section.small-section {
    max-width: 600px;
    margin-left: 0;
}
h1 {
    font-size: 22pt;
    margin-top: 10px;
    margin-bottom: 0;
    font-family: 'Roboto', sans-serif;
}
h2 {
    font-size: 18pt;
    margin-top: 10px;
    text-transform: uppercase;
    color: #424546;
    font-family: 'Roboto', sans-serif;
}
h3 {
    margin: 0;
    margin-bottom: 10px;
    text-transform: uppercase;
    font-size: 14pt;
    color: #424546;
    font-family: 'Roboto', sans-serif;
}
h4 {
    font-size: 12pt;
    font-weight: bold;
    color: #515556;
}
hr {
    margin-top: 10px;
    margin-bottom: 20px;
}
.button-bar {
    background-color: #daf2f7;
    margin: -20px;
    margin-bottom: 20px;
    z-index: 3;
    padding: 5px;
    color: white;
}
.button-bar a.router-link {
    margin-top: 8px;
    margin-left: 10px;
    margin-bottom: 5px;
    display: inline-block;
    cursor: pointer;
}
.button-bar a.btn {
    margin-bottom: -1px;
}
.btn.block-button {
    display: block;
    width: 100%;
    margin-bottom: 5px;
}
.table {
    margin-bottom: 0;
}
.row section {
    margin-bottom: 0;
    padding-bottom: 5px;
}
.col-amounts {
    width: 100px;
}
.icon-button {
    font-size: 22px;
    cursor: pointer;
}
.icon-button:hover {
    opacity: 0.8;
}
.icon-button.fa-remove {
    color: #b70000;
}
.icon-button.fa-plus {
    color: green;
}
.alert {
    margin-bottom: 5px;
}
.bg-outflows {
    background-color: #fce8e3;
}
input[type=number], td.number, th.number {
    text-align: right;
}
.table>tbody>tr>td.td-indent {
    padding-left: 15px;
}
tbody>tr>td.has-control, tbody>tr>th.has-control {
    padding-top: 1px;
    padding-bottom: 1px;
}
td.btn-col, th.btn-col {
    width: 30px;
}
thead .btn-col {
    width: 60px;
}
.table>thead>tr>th, .table>thead>tr>td, .table>tbody>tr>td {
    vertical-align: middle;
}
.table .fa-remove:hover {
    color: red;
}
tr:hover .fa-remove {
    display: inline-block;
}
.offline-flag {
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    padding: 5px;
    text-align: center;
    z-index: 5;
}
.well {
    padding: 10px;
}
@media (max-width: 1200px) {
.section-wrapper.menuShowing {
      margin-left: 0;
}
}
@media (max-width: 500px) {
section {
      padding: 10px;
}
.button-bar {
      margin: -10px;
      margin-bottom: 10px;
}
.col2-control {
      display: block;
      width: 100%;
}
.vdp-datepicker {
      display: block;
      width: 100%;
}
}




