
header {
	font-family: 'Roboto', sans-serif;
}
.section-wrapper.menuShowing {
	margin-left: 250px;
}
.page-heading {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	background-color: rgba(20,15,10,1);
	background-color: #003540;
	color: #fff;
	padding: 5px;
	-webkit-transition: left 0.5s;
	transition: left 0.5s;
	min-width: 320px;
	z-index: 11;
	min-height: 50px;
}
.page-heading.menuShowing {
	left: 250px;
}
.page-heading h1 {
	display: inline-block;
	vertical-align: top;
	margin: 0;
	margin-top: 8px;
	margin-left: 20px;
	font-size: 22px;
}
.page-heading h1 small {
	color: #9a9a9a
}
.page-heading .menu-list {
	float: right;
	color: #e8e8e8;
	margin: -5px;
}
.page-heading .menu-list li {
	display: inline-block;
	text-align: center;
}
.page-heading .menu-list li i {
	font-size: 20px;
}
.page-heading .menu-list li a {
	font-size: 12px;
	color: inherit;
	padding: 5px;
	display: block;
}
.page-heading .menu-list li:hover a, .page-heading .menu-list .router-link-exact-active {
	text-decoration: none;
	cursor: pointer;
	color: #003540;
	background-color: #daf2f7;
}
.new-transaction-button-container {
	position: absolute;
	top: 13px;
	right: 15px;
}
.header-flag {
	position: absolute;
	top: 5px;
	right: 45px;
	padding: 5px;
	border-radius: 4px;
}
.offline-flag {
	background-color: #ff4747;
	color: white;
}
.transactions-synced-flag {
	background-color: #4CB050;
	color: white;
}
.update-alert:hover {
	cursor: pointer;
	-webkit-box-shadow: 0 0 5px white;
	        box-shadow: 0 0 5px white;
}
.menu {
	width: 250px;
	padding: 0;
	padding-top: 0;
	background-color: rgba(20,15,10,1);
	background-color: #003540;
	color: #fff;
	position: fixed;
	top: 0;
	left: -250px;
	bottom: 0;
	overflow-y: auto;
	-webkit-transition: left 0.5s;
	transition: left 0.5s;
	z-index: 10;
}
.menu-block {
	border-left: 3px solid #2fff96;
	padding: 10px;
	padding-top: 0;
	padding-bottom: 0;
}
.menu-button-heading {
	cursor: pointer;
	background-color: #03A9F4;
	padding: 12.5px;
	text-decoration: none;
	margin-left: -10px;
	margin-right: -10px;
	margin-top: 0;
}
.menu-button-heading:hover {
	opacity: 0.8
}
.menu.menuShowing {
	left: 0;
}
.menu-list {
	list-style: none;
	margin: 0;
	padding: 0;
}
.navbar-logo {
	background-color: white;
	margin-left: -10px;
	margin-right: -10px;
	width: calc(50% + 20px);
}
header nav h2 {
	font-size: 12pt;
	text-transform: uppercase;
	margin-top: 20px;
	text-decoration: underline;
	color: inherit;
}
header nav h3 {
	font-size: 11pt;
	text-transform: uppercase;
	margin-top: 20px;
}
.menu li a {
	color: #e8e8e8;
	display: block;
	padding: 5px;
	text-decoration: none;
	font-size: 18px;
	-webkit-transition: -webkit-transform 0.2s;
	transition: -webkit-transform 0.2s;
	transition: transform 0.2s;
	transition: transform 0.2s, -webkit-transform 0.2s;
}
.menu li a:hover {
	text-decoration: none;
	color: white;
	background-color: rgba(255,255,255,0.1);
	-webkit-transform: scale(1.05, 1.05);
	        transform: scale(1.05, 1.05);
}
.menu-button {
	font-size: 32px;
	cursor: pointer;
	display: inline-block;
	line-height: 0;
	margin-top: 4px;
}
.menu .menu-button {
	float: right;
}
.bank-accounts {
	list-style: none;
	padding: 0;
}
.bank-balance {
	text-align: right;
	float: right;
}
.bank-accounts li {
	padding-top: 2px;
	padding-bottom: 2px;
	-webkit-transition: -webkit-transform 0.2s;
	transition: -webkit-transform 0.2s;
	transition: transform 0.2s;
	transition: transform 0.2s, -webkit-transform 0.2s;
}
.bank-accounts .account:hover {
	background-color: rgba(255,255,255,0.1);
	-webkit-transform: scale(1.03, 1.03);
	        transform: scale(1.03, 1.03);
	cursor: pointer;
}
.bank-accounts .total {
	border-top: 1px solid #a0a0a0;
	margin-top: 5px;
}
header .icon-button.fa-plus {
	color: #fff;
	margin-top: -3px;
}
header .text-danger {
	color: #fd5757;
}
header .text-success {
	color: #60ef62;
}
.main-alert {
	position: fixed;
	top: 5px;
	right: 5px;
	z-index: 50
}
.side-menu-button {
	display: none;
}
@media (max-width: 1050px) {
.section-wrapper.menuShowing {
		margin-left: 0;
}
}
@media (max-width: 800px) {
.page-heading .menu-list {
		display: none;
}
}
@media (max-width: 450px) {
.header-flag span {
		display: none;
}
.side-menu-button {
		display: block;
		opacity: 0;
		position: fixed;
		top: calc(50% - 20px);
		right: 0;
		z-index: 10;
		color: #003540;
}
}



